import { useState, useEffect, useMemo } from 'react';
import { useRouter } from 'next/router';
import { api } from '../../services/api';
import { useToast } from '../../components/ui/ToastProvider';
import styles from './product-sales.module.css';

interface ProductSale {
  _id: string;
  name: string;
  category: string;
  unitsSold: number;
  revenue: number;
  avgSellingPrice: number;
  ordersCount: number;
  currentStock: number;
  stockStatus: 'in' | 'low' | 'out';
}

interface KPIs {
  totalUnitsSold: number;
  totalRevenue: number;
  bestSellingProduct: string;
  zeroSalesProducts: number;
  cancellationRate: number;
}

export default function ProductSalesPage() {
  const router = useRouter();
  const { productId: productIdQuery } = router.query as { productId?: string };
  const { pushToast } = useToast();
  const [vendorId, setVendorId] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [search, setSearch] = useState('');
  const [searchDebounced, setSearchDebounced] = useState('');
  const [category, setCategory] = useState('');
  const [stockStatus, setStockStatus] = useState('');
  const [minUnitsSold, setMinUnitsSold] = useState('');
  const [minRevenue, setMinRevenue] = useState('');
  const [zeroSalesOnly, setZeroSalesOnly] = useState(false);
  
  const [products, setProducts] = useState<ProductSale[]>([]);
  const [kpis, setKPIs] = useState<KPIs>({
    totalUnitsSold: 0,
    totalRevenue: 0,
    bestSellingProduct: 'â€”',
    zeroSalesProducts: 0,
    cancellationRate: 0,
  });
  
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalCount, setTotalCount] = useState(0);
  const [sortBy, setSortBy] = useState('revenue');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  const limit = 10;

  useEffect(() => {
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem('cc_user');
        if (stored) {
          const parsed = JSON.parse(stored);
          setVendorId(parsed.id);
        }
      } catch {}
    }
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      setSearchDebounced(search);
    }, 300);
    return () => clearTimeout(timer);
  }, [search]);

  useEffect(() => {
    if (vendorId) {
      loadData();
    }
  }, [vendorId, startDate, endDate, searchDebounced, category, stockStatus, minUnitsSold, minRevenue, zeroSalesOnly, page, sortBy, sortOrder, productIdQuery]);

  const loadData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const filters = {
        startDate: startDate || undefined,
        endDate: endDate || undefined,
        search: searchDebounced || undefined,
        category: category || undefined,
        stockStatus: stockStatus || undefined,
        minUnitsSold: minUnitsSold ? Number(minUnitsSold) : undefined,
        minRevenue: minRevenue ? Number(minRevenue) : undefined,
        zeroSalesOnly: zeroSalesOnly || undefined,
        page,
        limit,
        sortBy,
        sortOrder,
        productId: productIdQuery || undefined,
      };

      const [analyticsData, kpisData] = await Promise.all([
        api.productSales.getAnalytics(vendorId, filters),
        api.productSales.getKPIs(vendorId, { startDate, endDate }),
      ]);

      setProducts(analyticsData.products || []);
      setTotalPages(analyticsData.totalPages || 1);
      setTotalCount(analyticsData.totalCount || 0);
      setKPIs(kpisData);
    } catch (err: any) {
      setError(err.message || 'Failed to load data');
      pushToast({ type: 'error', title: 'Load Failed', message: err.message });
    } finally {
      setLoading(false);
    }
  };

  const handleSort = (column: string) => {
    if (sortBy === column) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortBy(column);
      setSortOrder('desc');
    }
  };

  const handleRowClick = (productId: string) => {
    router.push(`/vendor/product-sales/${productId}`);
  };

  const getSortIcon = (column: string) => {
    if (sortBy !== column) return 'â‡…';
    return sortOrder === 'asc' ? 'â†‘' : 'â†“';
  };

  const categories = useMemo(() => {
    const cats = new Set<string>();
    products.forEach((p) => p.category && cats.add(p.category));
    return Array.from(cats).sort();
  }, [products]);

  if (error) {
    return (
      <div className={styles['product-sales-page']}>
        <div className={styles['ps-error-state']}>
          <h3>Error Loading Data</h3>
          <p>{error}</p>
          <button className={styles['ps-retry-button']} onClick={loadData}>
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className={styles['product-sales-page']}>
      <div className={styles['ps-header']}>
        <h1 className={styles['ps-title']}>Product Sales Analytics</h1>
        <div className={styles['ps-date-selector']}>
          <label>From:</label>
          <input
            type="date"
            className={styles['ps-date-input']}
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
          />
          <label>To:</label>
          <input
            type="date"
            className={styles['ps-date-input']}
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
          />
        </div>
      </div>

      {loading && !products.length ? (
        <div className={styles['ps-kpi-grid']}>
          {[1, 2, 3, 4, 5].map((i) => (
            <div key={i} className={`${styles['ps-kpi-card']} ${styles['ps-skeleton']}`} style={{ height: '100px' }} />
          ))}
        </div>
      ) : (
        <div className={styles['ps-kpi-grid']}>
          <div className={styles['ps-kpi-card']}>
            <div className={styles['ps-kpi-label']}>Total Units Sold</div>
            <div className={styles['ps-kpi-value']}>{kpis.totalUnitsSold.toLocaleString()}</div>
            <div className={styles['ps-kpi-subtext']}>Across all products</div>
          </div>
          <div className={styles['ps-kpi-card']}>
            <div className={styles['ps-kpi-label']}>Total Revenue</div>
            <div className={styles['ps-kpi-value']}>${kpis.totalRevenue.toLocaleString(undefined, { minimumFractionDigits: 2 })}</div>
            <div className={styles['ps-kpi-subtext']}>From completed orders</div>
          </div>
          <div className={styles['ps-kpi-card']}>
            <div className={styles['ps-kpi-label']}>Best Selling Product</div>
            <div className={styles['ps-kpi-value']} style={{ fontSize: '1.25rem' }}>{kpis.bestSellingProduct}</div>
            <div className={styles['ps-kpi-subtext']}>By units sold</div>
          </div>
          <div className={styles['ps-kpi-card']}>
            <div className={styles['ps-kpi-label']}>Zero-Sales Products</div>
            <div className={styles['ps-kpi-value']}>{kpis.zeroSalesProducts}</div>
            <div className={styles['ps-kpi-subtext']}>No sales in period</div>
          </div>
          <div className={styles['ps-kpi-card']}>
            <div className={styles['ps-kpi-label']}>Cancellation Rate</div>
            <div className={styles['ps-kpi-value']}>{kpis.cancellationRate.toFixed(1)}%</div>
            <div className={styles['ps-kpi-subtext']}>Orders cancelled</div>
          </div>
        </div>
      )}

      <div className={styles['ps-search-filter-row']}>
        <div className={styles['ps-search-box']}>
          <input
            type="text"
            className={styles['ps-search-input']}
            placeholder="Search by product name, SKU, or category..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
        <div className={styles['ps-filter-grid']}>
          <div className={styles['ps-filter-group']}>
            <label className={styles['ps-filter-label']}>Category</label>
            <select
              className={styles['ps-filter-select']}
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value="">All Categories</option>
              {categories.map((cat) => (
                <option key={cat} value={cat}>{cat}</option>
              ))}
            </select>
          </div>
          <div className={styles['ps-filter-group']}>
            <label className={styles['ps-filter-label']}>Stock Status</label>
            <select
              className={styles['ps-filter-select']}
              value={stockStatus}
              onChange={(e) => setStockStatus(e.target.value)}
            >
              <option value="">All</option>
              <option value="in">In Stock</option>
              <option value="low">Low Stock</option>
              <option value="out">Out of Stock</option>
            </select>
          </div>
          <div className={styles['ps-filter-group']}>
            <label className={styles['ps-filter-label']}>Min Units Sold</label>
            <input
              type="number"
              className={styles['ps-filter-input']}
              value={minUnitsSold}
              onChange={(e) => setMinUnitsSold(e.target.value)}
              min="0"
            />
          </div>
          <div className={styles['ps-filter-group']}>
            <label className={styles['ps-filter-label']}>Min Revenue ($)</label>
            <input
              type="number"
              className={styles['ps-filter-input']}
              value={minRevenue}
              onChange={(e) => setMinRevenue(e.target.value)}
              min="0"
            />
          </div>
          <div className={styles['ps-filter-group']}>
            <div className={styles['ps-filter-checkbox']}>
              <input
                type="checkbox"
                id="zeroSales"
                checked={zeroSalesOnly}
                onChange={(e) => setZeroSalesOnly(e.target.checked)}
              />
              <label htmlFor="zeroSales">Zero-sales only</label>
            </div>
          </div>
        </div>
      </div>

      <div className={styles['ps-table-wrapper']}>
        {loading ? (
          <div>
            {[1, 2, 3, 4, 5].map((i) => (
              <div key={i} className={`${styles['ps-skeleton']} ${styles['ps-skeleton-row']}`} />
            ))}
          </div>
        ) : products.length === 0 ? (
          <div className={styles['ps-empty-state']}>
            <div style={{ fontSize: '4rem', opacity: 0.3 }}>ðŸ“Š</div>
            <h3>No Sales Data</h3>
            <p>No products match your filters or no sales have been recorded yet.</p>
          </div>
        ) : (
          <>
            <table className={styles['ps-table']}>
              <thead className={styles['ps-table-head']}>
                <tr>
                  <th onClick={() => handleSort('name')}>
                    Product Name <span className={styles['ps-sort-icon']}>{getSortIcon('name')}</span>
                  </th>
                  <th onClick={() => handleSort('category')}>
                    Category <span className={styles['ps-sort-icon']}>{getSortIcon('category')}</span>
                  </th>
                  <th onClick={() => handleSort('unitsSold')}>
                    Units Sold <span className={styles['ps-sort-icon']}>{getSortIcon('unitsSold')}</span>
                  </th>
                  <th onClick={() => handleSort('revenue')}>
                    Revenue <span className={styles['ps-sort-icon']}>{getSortIcon('revenue')}</span>
                  </th>
                  <th onClick={() => handleSort('avgSellingPrice')}>
                    Avg Price <span className={styles['ps-sort-icon']}>{getSortIcon('avgSellingPrice')}</span>
                  </th>
                  <th onClick={() => handleSort('ordersCount')}>
                    Orders <span className={styles['ps-sort-icon']}>{getSortIcon('ordersCount')}</span>
                  </th>
                  <th onClick={() => handleSort('currentStock')}>
                    Stock <span className={styles['ps-sort-icon']}>{getSortIcon('currentStock')}</span>
                  </th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody className={styles['ps-table-body']}>
                {products.map((product) => (
                  <tr key={product._id} onClick={() => handleRowClick(product._id)}>
                    <td className={styles['ps-product-name']}>{product.name}</td>
                    <td>{product.category || 'â€”'}</td>
                    <td>{product.unitsSold.toLocaleString()}</td>
                    <td>${product.revenue.toLocaleString(undefined, { minimumFractionDigits: 2 })}</td>
                    <td>${product.avgSellingPrice.toFixed(2)}</td>
                    <td>{product.ordersCount}</td>
                    <td>{product.currentStock}</td>
                    <td>
                      <span className={`${styles['ps-status-badge']} ${styles[product.stockStatus]}`}>
                        {product.stockStatus === 'in' ? 'In Stock' : product.stockStatus === 'low' ? 'Low Stock' : 'Out'}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div className={styles['ps-pagination']}>
              <div className={styles['ps-pagination-info']}>
                Showing {(page - 1) * limit + 1} - {Math.min(page * limit, totalCount)} of {totalCount}
              </div>
              <div className={styles['ps-pagination-buttons']}>
                <button
                  className={styles['ps-pagination-button']}
                  onClick={() => setPage((p) => Math.max(1, p - 1))}
                  disabled={page === 1}
                >
                  Previous
                </button>
                <button
                  className={styles['ps-pagination-button']}
                  onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
                  disabled={page === totalPages}
                >
                  Next
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
